'use strict';
import {JsonPortMarshaler} from "./marshaler";
import {ITooltipProvider} from "../interfaces/iTooltipProvider";
import {PopupDisplayParameters} from "../common/popupDisplayParameters";
export class TooltipMarshaler extends JsonPortMarshaler {
    constructor(public tooltipProvider: ITooltipProvider) {
        super();
    }
    
    public hostContentInPopup(config: PopupDisplayParameters): void {
        this.tooltipProvider.hostContentInPopup(config);
    }
    public dismissPopup() {
        return this.tooltipProvider.dismissPopup();
    }
}